import { bookingsSafeData as MOCK_APIRESP } from './bookingsSafeData';
import { _Date, TIME_SLOTS } from "../../../helpers/_Date";

// DATA FOR TABLE EXAMPLE
export const BOOKINGS_DATA = MOCK_APIRESP.map((rec) => parseRawSourseDataRec(rec));


function parseRawSourseDataRec(record) {
    const _notesStrTmp = 'some notes **erqwerqwr*gfdsbvbcxfgerfrfrfcrfr*vrvbrewrgvbbh'

    return {
        'id'     : record['id']      || '',
        'history': record['history'] || '',
        'time'   : record['time']    || '',
        'tarif'  : record['tarif']   || '',
        'minutes': record['timeVal'] || 0,
        'flyer'  : record['flyer']   || '',
        'flyerId': record['flyerId'] || 0,
        'email'  : record['email']   || '',
        'phone'  : record['phone']   || '',
        'status' : record['status']  || '---',
        'notes'  : record['notes'] ? _notesStrTmp.slice(0, record['notes'].length) : '',
    };
}

function initDateTimeblocks() {
    const res = {}
    TIME_SLOTS.forEach(
        (slotN, i) => {res[i] = [] }
    )
    return res
}

export function API_GET_TIMEBLOCKS(BOOKINGS_DATA) {
    let TIMEBLOCKS_OBJ = {}
    BOOKINGS_DATA.forEach(reservation => {
        const time = new Date(reservation.time)
        const flightDateN = _Date.dateMsToDateN(time);
        const timeSlotN = _Date.msToSlotN(time);

        const timeVal       = +reservation.minutes
        const flyer         = reservation.flyer
        const id            = reservation.id
        if (!id) {
            //debugger
             console.log(reservation)
        }
        const tarifName     = reservation.tarif
        const paymentData   = reservation.status
        // const flyerData     = {
        //                           id:    reservation['flyerId'],
        //                           email: reservation['email'],
        //                           phone: reservation['phone'],
        //                       }
        const notes         = reservation.notes

        const additionalData = {
            'history' : reservation['history'],
            'flyerId' : reservation['flyerId'],
            'email'   : reservation['email'],
            'phone'   : reservation['phone'],
        }

        if (flightDateN && !TIMEBLOCKS_OBJ[flightDateN]) {
            TIMEBLOCKS_OBJ[flightDateN] = initDateTimeblocks()
        }

        if (timeSlotN) {
            TIMEBLOCKS_OBJ[flightDateN][timeSlotN].push({
                id         ,
                timeVal    ,
                tarifName  ,
                flyer      ,
                paymentData,
                notes      ,
                additionalData,
            })
        }
    });

    return TIMEBLOCKS_OBJ
}
