function convertData(reservationsRawData) {
    const converted = reservationsRawData.map(
        record => {
            const flyerId = record[8]
            const newFlyerName = `Flyer${flyerId} Lname MiddleName`
            const newEmail = `Flyer${flyerId}@${record[9].split('@')[1]}`
            const newPhone = 7_111_100_00_00 + (+flyerId || 0)

            let newRecord = {
                id      : record[0],
                history : record[1],
                time    : record[2],
                timeVal : record[3],
                tarif   : record[4],
                flyer   : newFlyerName,
                notes   : record[6],
                status  : record[7],
                flyerId,
                email : newEmail,
                phone : newPhone,
            }
            return newRecord
        })

    return converted
}

