import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import './DaysContainer.css';


import { _Date, TIME_SLOTS } from '../../../helpers/_Date';
import { DayBlockDiv } from '../DayBlockDiv/DayBlockDiv';


export function DaysContainer({dateTimeblocks}) {
    // console.log(dateTimeblocks)
    // console.log(Object.entries(dateTimeblocks))
    const DayBlocks = Object.entries(dateTimeblocks).map(
        ([dateN, dateTimeblocks]) => <DayBlockDiv
            key={dateN}
            dateTimeblocks={dateTimeblocks}
        />

    )


    return (
        <div className="daysContainer">
            {DayBlocks}
        </div>
    )
};