import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import './TimeSlotDiv.css';

import { ReservationDiv } from '../ReservationDiv/ReservationDiv';
import { SlotTimeHeader } from './SlotTimeHeader';
import { SLotBottomLine } from './SLotBottomLine';



export const FreeTimeSlot = ({time}) => (
    <div className="timeSlot" key={time}>
        <SlotTimeHeader
            time = {time}
        />
        <div className="reservationsContainer">
            FREE SLOT
            {/* <SLotBottomLine
                expanded={expandAll}
                setExpandAll={setExpandAll}
            /> */}
        </div>
    </div>
)

export function TimeSlotDiv({time, reservationsArr}) {
    const [expandAll, setExpandAll] = useState(false);

    if (!reservationsArr?.length) return FreeTimeSlot({time})

    const reservations = reservationsArr.map(
        reservation => {
            return <ReservationDiv
                key={reservation.id}
                time={time}
                blockExpanded={expandAll}
                reservation={reservation}
            />
        }
    )

    return (
        <div className="timeSlot">
            <SlotTimeHeader
                time = {time}
            />
            <div className="reservationsContainer">
                {reservations}
                <SLotBottomLine
                    expanded={expandAll}
                    setExpandAll={setExpandAll}
                />
            </div>
        </div>
    )
};